<template>
    <el-menu-item
            v-if="!item.children || item.children.length === 0"
            :index="item.path">
        <p :title="item.name">
            {{item.name}}
        </p>
        <router-link :to="item.path"></router-link>
    </el-menu-item>
    <el-submenu
            v-else
            :index="item.path">
        <template slot="title">
            <p :title="item.name">
                {{item.name}}
            </p>
        </template>
        <sidebar-item
                v-for="child in item.children"
                v-if="!child.hidden"
                :key="child.path"
                :item="child"></sidebar-item>
    </el-submenu>
</template>

<script>
    export default {
        name: 'SidebarItem',
        props: {
            item: {
                type: Object,
                required: true
            }
        }
    }
</script>

<style lang="scss" scoped="">
    .sidebar-container {
        .el-menu-item {
            position: relative;

            a {
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                z-index: 1;
            }
        }
    }
</style>



