<template>
    <el-menu
            mode="vertical"
            theme="dark"
            :default-active="$route.path"
            :unique-opened="true">
        <sidebar-item
                v-for="item in permission_routers"
                v-if="!item.hidden"
                :key="item.path"
                :item="item"></sidebar-item>
    </el-menu>
</template>

<script>
import { mapGetters } from 'vuex'
import SidebarItem from "./SidebarItem";

export default {
	components: {SidebarItem},
    computed: {
    ...mapGetters([
      'permission_routers'
    ])
  }
}
</script>

<style lang="scss">
    .sidebar-container {
        .el-submenu .el-menu-item {
            padding-right: 20px;
        }

        .el-submenu__icon-arrow {
            right: 10px;
        }

        p {
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }
</style>



